 
:root {
  --color-primary-blue600: #0052cc;
  --color-primary-dark900: #091e42;
  --color-primary-red500: #ff0008;
  --color-primary-red300: #ff2d34;
  --color-primary-light0: #ffffff;

  --color-secondary-green500: #36b37e;
  --color-secondary-dark: #039946;
  --color-secondary-yellow500: #ffab00;
  --color-secondary-purple500: #6554c0;

  --color-dark900: #091e42;
  --color-dark800: #172b4d;
  --color-dark700: #253858;
  --color-dark600: #344663;
  --color-dark500: #42526e;

  --color-mid900: #505f79;
  --color-mid800: #5e6c84;
  --color-mid700: #6b778c;
  --color-mid600: #97a0af;
  --color-mid500: #a5adba;
  --color-mid400: #b3bac5;

  --color-light500: #c1c7d0;
  --color-light400: #dfe1e6;
  --color-light300: #ebecf0;
  --color-light200: #f4f5f7;
  --color-light100: #fafbfc;

  --color-background: #F9F9F9;

  --color-primary-lighter: #9871f5;
  --color-primary-light: #916bea;
  --color-primary: #8257e5;
  --color-primary-dark: #774dd6;
  --color-primary-darker: #6842c2;

  --color-secondary: #04d361;
  --color-title-in-primary: #ffffff;
  --color-text-title: #32264d;
  --color-text-complement: #9c98a6;
  --color-line-in-white: #e6e6f0;
  --color-input-background: #f8f8fc;
  --color-button-text: #ffffff;
  --color-box-base: #ffffff;
  --color-box-footer: #fafafc;
  --color-small-info: #c1bccc;

  /* tamanho da fonte padr√£o: 16px - 100% - 1rem */
  font-size: 60%; /* controle das medidas rem */
  max-width: 120%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  background-color: var(--color-background);
  overflow-x: hidden;
}


body,
input,
button,
textarea {
  /*font: 500 1.6rem Poppins;*/
  font-weight: 600;
  font-size: 1.6rem;
  font-family: Poppins;
  color: var(--color-dark500);
}

.container {
  /* box model*/
  width: 90vw;
  max-width: 700px;
}


@media (min-width: 700px) {
  :root {
    font-size: 60, 2%; /*todo 1rem vai ser relativo a 10px*/
  }
}
